<template>
    <transition name="move">
        <div v-show="showFlag" class="food" ref="food">
            <div class="food-content">
                <div class="image-header">
                    <img :src="food.image">
                    <div class="back">
                        <i class="icon-arrow_lift" @click="hide"></i>
                    </div>
                </div>
                <div class="content">
                    <h1 class="title">{{ food.name }}</h1>
                    <div class="detail">
                        <span class="sell-count">月售{{ food.sellCount }}份</span>
                        <span class="rating">好评率{{ food.rating }}%</span>
                    </div>
                    <div class="price">
                        <span class="now">￥{{ food.price }}</span>
                        <span class="old" v-show="food.oldPrice">￥{{ food.oldPrice }}  </span>
                    </div>
                    <transition name="fade">
                        <div class="cartcontrol-wrapper">
                            <cart-control :food="food"></cart-control>
                        </div>
                    </transition>
                    <transition name="fade">
                        <div @click.stop="addFirst" class="buy" v-show="!food.count || food.count === 0">加入购物车</div>
                    </transition>
                </div>
                <split v-show="food.info"></split>
                <div class="info" v-show="food.info">
                    <h1 class="title">商品消息</h1>
                    <p class="text">{{ food.info }}</p>
                </div>
                <split></split>
                <div class="rating">
                    <h1 class="title">商品评价</h1>
                    <rating-select :select-type.sync="selectType"
                    :only-content.sync="onlyContent" :desc.sync="desc" :ratings="food.ratings" @rating-type-select="ratingTypeSelect"
                    @content-toggle="contentToggle"></rating-select>
                </div>
            </div>
        </div>
    </transition>

</template>

<script type="text/ecmascript-6">
    import Vue from 'vue'
    import Bus from '../../bus'
    import BScroll from 'better-scroll'
    import CartControl from '../../cartcontrol/cartcontrol.vue'
    import Split from '../split/split.vue'
    import RatingSelect from '../ratingselect/ratingselect.vue'


    const POSITIVE = 0;
    const NEGATIVE = 1;
    const ALL = 2;

    export default{
        components:{
            CartControl,
            Split,
            RatingSelect
        },
        props:{
            food:{
                type:Object
            }
        },
        data(){
          return {
              showFlag: false,
              selectType:ALL,
              //只看有内容的评价
              onlyContent:false,
              desc:{
                  all: '全部',
                  positive: '推荐',
                  negative: '吐槽'
              }
          }
        },
        methods:{
            show() {
                this.showFlag = true;
                this.selectType = ALL;
                this.onlyContent = true;
                this.$nextTick( () => {
                    console.log(this.scroll)
                    if( !this.scroll ) {
                        this.scroll = new BScroll(this.$refs.food,{
                            click: true
                        });
                    }else {
                        this.scroll.refresh();
                    }
                })
            },
            hide(){
                 this.showFlag = false;
            },
            addFirst(event){
                if( ! event._constructed){
                    return;
                }
                Vue.set(this.food,'count',1);
                //小球传递
                Bus.$emit('cart-add',event.target)
            },
            //ratingselect子组件传值
            ratingTypeSelect(type){
                console.log(type)
            },
            contentToggle(content){
                console.log(content)
            }
        }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>

    .food
        position fixed
        left: left
        top:0
        bottom 48px
        z-index 30
        width 100%
        background #fff
        &.move-enter-active,&.move-leave-active
            transition all 0.2s linear
        &.move-enter,&.move-leave-to
            transform translateX(100%)
        .image-header
            position: relative
            width:100%
            height 0
            padding-top 100%
            img
                position absolute
                top: 0
                left 0
                width 100%
                height 100%
            .back
                position absolute
                top: 10px
                left 10px
                .icon-arrow_lift
                    display block
                    padding 10px
                    font-size 20px
                    color #fff
        .content
            padding 18px
            position relative
            .title
                line-height 14px
                margin-bottom 8px
                font-size 14px
                font-weight bold
                color rgb(7,17,27)
            .detail
                height 10px
                margin-bottom 18px
                line-height 10px
                font-size 0
                .sell-count,.rating
                    font-size 10px
                    color rgb(147,153,159)
                .sell-count
                    margin-right  12px
            .price
                font-weight bold
                line-height: 24px
                .now
                    margin-right 18px
                    font-size 14px
                    color rgb(240,20,20)
                .old
                    text-decoration line-through
                    font-size 10px
                    color rgb(147,153,159)
        .cartcontrol-wrapper
            position absolute
            right 12px
            bottom 12px
            z-index 10
        .buy
            position absolute
            right 18px
            bottom 18px
            z-index 10
            height 24px
            line-height 24px
            padding 0 12px
            box-sizing border-box
            border-radius 12px
            font-size 10px
            color #fff
            background rgb(0,160,220)
            opacity 1
        .fade-enter-active,.fade-leave-active
            transition all .5s
        .fade-enter,.fade-leave-to
            opacity 0
        .info
            padding 18px
            .title
                line-height 14px
                margin-bottom 6px
                font-size 14px
                color rgb(7,17,27)
            .text
                line-height 24px
                padding 0 8px
                font-size 12px
                color:rgb(77,85,93)
        .rating
            padding-top 18px
            .title
                padding: 0 18px;
                line-height 14px
                margin-bottom 6px
                font-size 14px
                color rgb(7, 17 ,27)

</style>


