<template>
    <div class="ratingselect">
        <div class="rating-type">
            <span @click="select(2,$event)" class="block positive" :class="{'active':selectTypeChild === 2}">
                {{ desc.all }}
                <span class="count">{{ ratings.length }}</span>
            </span>
            <span @click="select(0,$event)" class="block positive" :class="{'active' : selectTypeChild === 0}">
                {{ desc.positive }}
                <span class="count">{{ positives.length }}</span>    
            </span>
            <span @click="select(1,$event)" class="block negative" :class="{'active' : selectTypeChild === 1}">
                {{ desc.negative  }}
                <span class="count">{{ negative.length }}</span>
            </span>
            
        
        </div>
        <div @click="toggleContent($event)" class="switch" :class="{ 'on' : myOnlyContent }">
            <span class="icon-check_circle"></span>
            <span class="text">只看有内容的评价</span>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    const POSITIVE = 0;
    const NEGATIVE = 1;
    const ALL = 2;

    export default {
        props:{
            ratings:{
                type:Array,
                default(){
                    return []
                }
            },
            selectType:{
                type:Number,
                default: ALL
            },
            onlyContent:{
                type:Boolean,
                default: false
            },
            desc:{
                type:Object
            }
        },
        data(){
            return{
                 selectTypeChild:this.selectType,
                 myOnlyContent: this.onlyContent
            } 
        },
        computed:{
            //过滤推荐评价数量
            positives(){
                return this.ratings.filter( (rating) => {
                    return rating.rateType === POSITIVE;
                })
            },
            //过滤吐槽评价数量
            negative () {
                return this.ratings.filter( (rating) => {
                    return rating.rateType === NEGATIVE;
                })
            }

        },
        methods:{
            //商品评价按钮切换
            select(type,event){
                if(!event._constructed) {
                    return;
                }
                this.selectTypeChild = type;
                this.$emit('rating-type-select',type);
            },
            //只看有内容的评价
            toggleContent(event){
                if(!event._constructed) {
                    return;
                }
                this.myOnlyContent = !this.myOnlyContent;
                this.$emit('content-toggle',this.myOnlyContent)
            }
        }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
   @import "../../common/stylus/minxin";
    .ratingselect
        .rating-type
            padding 18px 0
            margin 0 18px
            border-1px(raba(7,17,27,.1))
            font-size 0
            .block 
                display inline-block
                padding 8px 12px
                margin-right 8px
                border-radius 1px
                font-size 12px
                color rgb(77,85,93) 
                &.active
                    color #fff 
                .count
                    margin-left 2px
                    font-size 8px
                &.positive
                    background rgba(0,160,220,.2)
                    &.active
                        background rgb(0,160,220)
                &.negative
                    background rgba(77,85,93,.2)
                    &.active
                        background rgb(77,85,93)
        .switch
            padding 12px 18px
            line-height 24px
            border-bottom 1px solid rgba(7,17,27,.1)
            color rgb(147,153,159)
            font-size 0
            &.on 
                .icon-check_circle
                    color #00c850
            .icon-check_circle 
                display inline-block
                vertical-align top
                margin-right 4px
                font-size 24px
            .text
                display inline
                vertical-align top
                font-size 12px

</style>


